import { Route, Switch, BrowserRouter, Redirect } from "react-router-dom";
import { useHistory, useRouteMatch } from "react-router";
import "antd/dist/antd.css";
import "./App.css";

function App() {
  const Router = () => {
    const { path } = useRouteMatch();
    const history = useHistory();
    return (
      <>
        <button onClick={() => history.push(`${path}home`)}>home</button>
        <button onClick={() => history.push(`${path}search-bus`)}>
          search
        </button>
        <Switch>
          <Route path={`${path}home`} render={() => <div>Home</div>} />
          <Route
            path={`${path}search-bus`}
            render={() => <div>Search Page</div>}
          />
          <Redirect to={`${path}home`} />
        </Switch>
      </>
    );
  };
  return (
    <div className="App">
      <BrowserRouter>
        <Router />
      </BrowserRouter>
    </div>
  );
}

export default App;
